/* -------------------------------------
        13 := section1 : liens
------------------------------------- */
footer{
    background-color: var(--background_bleu1);
    color: white;  
    font-weight: bold;
    font-size: 1.8rem;
    padding-bottom: 6rem;
}

footer section{
    padding: 3rem 5rem 4rem 5rem;
    justify-content: space-between;
}

footer section ul, footer form{
    width: 30%;
}

footer section li{
    margin-top: .2rem;
}

footer input{
    width: 90%;
    padding: .8rem 1rem;
    margin-top: .8rem;
}

footer input::placeholder{
    color: var(--background_bleu2);
    font-weight: bold;
    font-size: 1.1rem;
}



/* -------------------------------------
        14 := section2 : contact
------------------------------------- */
footer section + ul{
    padding-left: 4.5rem;
}

footer section + ul li{
    border: 2px solid white;
    border-radius: 50%;
    padding: 1.2rem 1.2rem .5rem 1.2rem;
    margin: 0 .5rem;
    font-size: 3rem;
}




/* --------------------------
        15 := responsive
-------------------------- */
@media screen and (min-width:768px) and (max-width:1000px){
    footer{
        font-size: 1.6rem;    
    }

    footer section{
        padding: 3rem 3.5rem 4rem 3.5rem;
    }

    footer section ul, footer form{
        width: 31%;
    }

    footer section + ul{
        padding-left: 2.5rem;
    }

    footer section + ul li{
        font-size: 2rem;
    }
}