/* -------------------------------------
        04 := section1 : navigation
------------------------------------- */
header{
    background-image: url(../../assets/Arri√®re\ plan.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    align-content: center;
}

.navigation{
    justify-content: space-between;
}

.logo{
    margin-left: 4%;
    margin-top: 1.5rem;
    width: 30%;
}

nav ul{
    padding-right: 1.5rem;
    padding-top: 2.5rem;
    font-size: 1.4rem;
}

nav ul li{
    padding-right: 2rem;
}

nav ul li:nth-child(4){
    padding-right: .5rem;
}

nav .person{
    font-size: 1.6rem;
}



/* -------------------------------------
        05 := section2 : photos
------------------------------------- */
header .sect2{
    width: 100%;
    margin-top: 2.2rem;
    justify-content: space-between;
    padding-bottom: 5rem;
}

header .papillon{
    height: 13rem;
    width: 15%;
    align-self: flex-end;
}

header .tournesol{
    height: 26rem;
    align-self: flex-start;
}

header .album{
    width: 65%;
    text-align: center;
}

header .album h1{
    font-size: 3rem;
    margin-top: 6rem;
    margin-bottom: 3rem;
}

header .album img{
    width: 50%;
    margin: auto;
}

header .album button{
    margin-top: 3rem;
    margin-bottom: 4rem;
}




